  height: 100%;
  vertical-align: middle;
}

.nav__item a {
  line-height: 80px;
  display: inline-block;
  width: 100%;
  height: 100%;
  color: inherit;
  text-decoration: none;
  padding: 0 10px;
  text-align: center;
  margin-right: 5px;
  box-sizing: border-box;
}

