}

.scroll__title {
  font-family: Gotham-book_regular;
  font-size: 0.8rem;
  margin-bottom: 15px;
}

.scroll__button {
  position: relative;
  margin: 0 auto;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid #f5f5f5;
  cursor: pointer;
}

.scroll__button:before {
  width: 18px;
  height: 13px;
  top: 50%;
  left: 50%;
  content: '';
  transform: translate(-50%, -50%);
  position: absolute;
  content: '';
  background: url("../images/icons/down.png") center no-repeat;
}

.logo {
  width: 60px;
  cursor: pointer;
}

.menu__bar1,
.menu__bar2,
.menu__bar3 {
  width: 35px;
  height: 5px;
  background-color: #11749e;
  margin: 6px 0;
  transition: 0.4s;
}

.change .menu__bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .menu__bar2 {
  opacity: 0;
}

.change .menu__bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}

